import mpwxs from './mpwxs'














export default {

	mixins: [mpwxs],














	props: {
		/**
		 * 按钮内容
		 */
		options: {
			type: Array,
			default () {
				return []
			}
		},
		info: {
			type: Object,
			default () {
				return {}
			}
		},
		/**
		 * 禁用
		 */
		disabled: {
			type: Boolean,
			default: false
		},
		/**
		 * 变量控制开关
		 */
		show: {
			type: Boolean,
			default: false
		},
		/**
		 * 是否自动关闭
		 */
		autoClose: {
			type: Boolean,
			default: true
		}
	},
	inject: ['swipeaction']
}
